import { Alert, Input, Select } from "@windmill/react-ui";
import React, { useEffect } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { Modal } from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import { useTranslation } from "react-i18next";
import { FiX } from "react-icons/fi";
import UploaderThree from "components/image-uploader/UploaderThree";
import Title from "components/form/Title";
import LabelArea from "components/form/LabelArea";
import Error from "components/form/Error";
import Uploader from "components/image-uploader/Uploader";
import DrawerButton from "components/form/DrawerButton";
import useAddFacilitySubmit from "hooks/useAddFacilitySubmit";

//internal import
const AddFacilityDrawer = ({ id, propertyList, getFacilityList }) => {
  const { t } = useTranslation();

  const {
    register,
    onSubmit,
    errors,
    openModal,
    imageUrl,
    setImageUrl,
    handleSubmit,
    onCloseModal,
    isSubmitting,
    tapValue,
    handleProductTap,
    handleProductSlug,
    handleSelectLanguage,
    handleSelectImage,
  } = useAddFacilitySubmit(id, getFacilityList);

  useEffect(() => {
    console.log(propertyList, "propertyList");
  }, [propertyList]);

  const containsSpecialCharsOrNumbers = (value) => {
    return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?0-9]/.test(value);
  };
  return (
    <>
      <Modal
        open={openModal}
        onClose={onCloseModal}
        center
        closeIcon={
          <div className="absolute top-0 right-0 text-red-500 focus:outline-none active:outline-none text-xl border-0">
            <FiX className="text-3xl" />
          </div>
        }
      >
        <div className="cursor-pointer">
          <UploaderThree
            imageUrl={imageUrl}
            setImageUrl={setImageUrl}
            handleSelectImage={handleSelectImage}
          />
        </div>
      </Modal>

      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {id ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Facility")}
            description={t(
              "Update your Facilities and necessary information from here"
            )}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Add Facility")}
            description={t(
              "Add your Facilities and necessary information from here"
            )}
          />
        )}
      </div>

      <Scrollbars className="track-horizontal thumb-horizontal w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)} className="block" id="block">
          {tapValue === "Basic Info" && (
            <div className="px-6 pt-8 flex-grow w-full h-full max-h-full pb-40 md:pb-32 lg:pb-32 xl:pb-32">
              {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label={t("ProductID")} />
                  <div className="col-span-8 sm:col-span-4">{productId}</div>
                </div> */}
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Facility Name")} />
                <div className="col-span-8 sm:col-span-4">
                  <Input
                    {...register("name", {
                      required: "Facility name required!",
                      validate: (value) =>
                        !containsSpecialCharsOrNumbers(value) ||
                        "Name cannot contain special characters or numbers",
                    })}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                    name="name"
                    type="text"
                    placeholder={t("Enter Facility Name ")}
                    onBlur={(e) => handleProductSlug(e.target.value)}
                  />
                  <Error errorName={errors.name} />
                </div>
              </div>

              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Select Property")} />
                <div className="col-span-8 sm:col-span-4">
                  <Select
                    // multiple
                    {...register(`property_id`, {
                      required: "Select property id !",
                    })}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  >
                    <option value="" hidden>
                      {t("Property ")}
                    </option>
                    {propertyList?.map((loc) => (
                      <option value={loc.id}>{loc.name}</option>
                    ))}
                  </Select>
                  <Error errorName={errors.property_id} />
                </div>
              </div>

              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Photos")} />
                <div className="col-span-8 sm:col-span-4">
                  <Uploader
                    product
                    folder="product"
                    imageUrl={imageUrl}
                    setImageUrl={setImageUrl}
                  />
                  <Alert className="sm12" type="info">
                    Image should be 300 × 210px and Image Size should be less
                    than 5mb
                  </Alert>
                  <Error errorName={errors.imageUrl} />
                </div>
              </div>
            </div>
          )}
          <DrawerButton
            id={id}
            save
            title="Facility"
            isSubmitting={isSubmitting}
            handleProductTap={handleProductTap}
          />
          <DrawerButton
            imageUrl={imageUrl}
            setImageUrl={setImageUrl}
            onCloseModal={onCloseModal}
            id={id}
            title=" Facility"
            isSubmitting={isSubmitting}
          />
        </form>
      </Scrollbars>
    </>
  );
};

export default React.memo(AddFacilityDrawer);
