import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useToggleDrawer from "hooks/useToggleDrawer";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import propertiesformlocal from "../../utils/Activities";
import MainDrawer from "components/drawer/MainDrawer";
import AddFacilityDrawer from "components/facilities/AddFacilities";
// import CheckBox from "components/form/CheckBox";
import useProductFilter from "hooks/useProductFilter";
import TableLoading from "components/preloader/TableLoading";
import { getAllCommonProperyList, getAllFacilityList } from "services/Api";
import FacilityList from "components/facilities/FacilityList";

const Facilities = () => {
  const { title, allId, serviceId, handleDeleteMany, handleUpdateMany } =
    useToggleDrawer();

  const { t } = useTranslation();
  const {
    toggleDrawer,
    lang,
    handleSubmitForAll,
    limitData,
  } = useContext(SidebarContext);

  const [name, setname] = useState("");
  const [loading, setLoading] = useState(true);
  const [pagenumber, setpagenumber] = useState(1);
  const [property_id, setproperty_id] = useState("");
  const [propertyList, setpropertyList] = useState([]);
  const [facilityList, setFacilityList] = useState([]);
  const [isCheckAll, setIsCheckAll] = useState(false);
  const [property, setproperty] = useState([]);
  const [isCheck, setIsCheck] = useState([]);
  const [wholeCount, setwholeCount] = useState({});

  const handleSelectAll = () => {
    setIsCheckAll(!isCheckAll);
    setIsCheck(propertiesformlocal.map((li) => li._id));
    if (isCheckAll) {
      setIsCheck([]);
    }
  };

  const { serviceData } = useProductFilter(propertiesformlocal);
  useEffect(() => {
    getPropertyList();
    getFacilityList(1);
  }, []);
  const getFacilityList = async (pagenumber, search = "") => {
    const FacilityList = await getAllFacilityList(pagenumber, search);
    setLoading(false);
    setFacilityList(FacilityList?.data?.data);
    setwholeCount(FacilityList?.data?.data?.pagination);
  };

  console.log( "==>" ,facilityList)

  const getPropertyList = async (pagenumber) => {
    const PropertyList = await getAllCommonProperyList(pagenumber);
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setLoading(false);
    setpropertyList(PropertyList?.data?.data?.propertyList);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getFacilityList(e);
  };

  const handleSearch = (search) => {
    getFacilityList(pagenumber, search);
  };

  return (
    <>
      <PageTitle>{t("Facilities")}</PageTitle>
      <MainDrawer>
        <AddFacilityDrawer
          name={name}
          setname={setname}
          id={serviceId}
          property_id={property_id}
          setproperty_id={setproperty_id}
          propertyList={propertyList}
          getFacilityList={getFacilityList}
        />
      </MainDrawer>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Button
                  onClick={toggleDrawer}
                  className="w-full rounded-md h-12"
                >
                  <span className="mr-2">
                    <FiPlus />
                  </span>
                  {t("Add Facilities")}
                </Button>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{t("S.no")}</TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell>{t("Property")}</TableCell>
                <TableCell className="text-center" width={"350px"}>
                  {t("Image")}
                </TableCell>
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <FacilityList
              lang={lang}
              isCheck={isCheck}
              products={facilityList?.facility}
              pagenumber={pagenumber}
              getFacilityList={getFacilityList}
              setIsCheck={setIsCheck}
              setproperty={setproperty}
              property={property}
              propertyList={propertyList}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={facilityList?.pagination?.total}
              resultsPerPage={facilityList?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Facilities;
