import { TableBody, TableCell, TableRow } from "@windmill/react-ui";
import MainDrawer from "components/Properties/EditPropertiesModal";
import EditFacility from "components/facilities/EditFacility";
import Tooltip from "components/tooltip/Tooltip";
import { SidebarContext } from "context/SidebarContext";
import { useContext, useState } from "react";
import { t } from "i18next";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import { getFacilitiesById } from "services/Api";
import DeleteFacilities from "./DeleteFacilities";

//internal import

const FacilityList = ({
  products,
  isCheck,
  property,
  setproperty,
  propertyList,
  getFacilityList,
  setIsCheck,
  wholeCount,
  pagenumber,
}) => {
  const { toggleModal, toggleDrawer1 } =
    useContext(SidebarContext);
  // const { title, serviceId } = useToggleDrawer();
  const [serviceId, setServiceId] = useState(1);
  const [title, settitle] = useState("");
  // const handleClick = (e) => {
  //   const { id, checked } = e.target;
  //   console.log("id", id, checked);

  //   setIsCheck([...isCheck, id]);
  //   if (!checked) {
  //     setIsCheck(isCheck.filter((item) => item !== id));
  //   }
  // };

  const handleUpdate = async (e) => {
    console.log(e.id);
    const singleview = await getFacilitiesById(e.id);
    if (singleview && singleview.status === 200) {
      console.log(singleview?.data?.data?.facility, "singleview");
      setproperty(singleview?.data?.data?.facility);
    }
    toggleDrawer1();
  };
  const handleModalOpen = (data) => {
    console.log(data, "DELETEsww", "ww");
    setServiceId(data?.id);
    settitle(data?.name);
    setproperty(data);
    toggleModal();
    // setTitle(title);
  };
  return (
    <>
      {isCheck?.length < 1 && (
        <DeleteFacilities
          property={property}
          id={serviceId}
          getFacilityList={getFacilityList}
          title={title}
        />
      )}
      <MainDrawer>
        <EditFacility
          pagenumber={pagenumber}
          isOpen={toggleDrawer1}
          onClose={toggleDrawer1}
          getFacilityList={getFacilityList}
          propertyList={propertyList}
          property={property}
        />
      </MainDrawer>
      <TableBody>
        {products?.map((facility, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-sm">
                {(wholeCount?.currentPage - 1) * wholeCount?.pageSize + i + 1}
              </span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm">{facility?.name}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <ul>
                {facility?.Facility_properties?.map((pro, i) => (
                  <li className="text-sm ">{pro?.Property?.name}</li>
                ))}
              </ul>
            </TableCell>
            <TableCell>
              {facility?.Facility_images?.map((img) => (
                <img
                  className="object-cover imgCustom"
                  src={img.image}
                  alt="Office"
                />
              ))}
            </TableCell>
            {/* <TableCell className="text-center">
                <ShowHideButton id={properties._id} status={properties.status} />
              </TableCell> */}
            <TableCell>
              {/* <EditDeleteButton
                  id={properties._id}
                  product={properties}
                  isCheck={isCheck}
                  handleUpdate={handleUpdate}
                  handleModalOpen={handleModalOpen}
                  title={showingTranslateValue(properties?.title, lang)}
                /> */}
              <button
                onClick={() => {
                  handleUpdate(facility);
                }}
                className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
              >
                <Tooltip
                  id="edit"
                  Icon={FiEdit}
                  title={t("Edit")}
                  bgColor="#10B981"
                />
              </button>
              <button
                onClick={() => handleModalOpen(facility)}
                className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
              >
                <Tooltip
                  id="delete"
                  Icon={FiTrash2}
                  title={t("Delete")}
                  bgColor="#EF4444"
                />
              </button>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default FacilityList;
